<template>
<div v-for='d in data' id = 'novels'>
<p>{{d.chapternumber}}</p>
<p>{{d.date}}</p>
<p>{{d.view}}</p>
</div>
</template>

<script>
import axios from 'axios'
export default{
    name:'Views',
    data(){
        return{
            novel:[],
            data:[]
        }
    },
    created(){
        this.novel = 'JVJSF6F'
        axios.get(`http://tententranslation.com/views?novel=${this.novel}`).then(response => {
            this.data = response.data
          })
          .catch(err => {
            console.log(err)
          })
    }
}
</script>